import Button from '@mui/material/Button'
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';
import { DataGrid } from '@mui/x-data-grid';
import React from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { CssBaseline, Link } from '@mui/material';

import { ApolloClient, InMemoryCache, gql } from '@apollo/client'
// import { BigNumber } from '@ethersproject/bignumber';

import {
  LATEST_BLOCK_QUERY,
  USER_VEJOE_QUERY,
  BOOSTED_PAIRS_QUERY,
  PAIRS_EXCHANGE_QUERY,
  USER_BALANCE_QUERY,
} from './queries';

import {
  LATEST_BLOCK_API_URL,
  VEJOE_API_URL,
  BOOSTED_API_URL,
  EXCHANGE_API_URL,
} from './constants'

// Borrowed from stack overflow...
const leftJoin = (objArr1, objArr2, key1, key2, defaults = {}) => objArr1.map(
  anObj1 => ({
    ...defaults,
    ...objArr2.find(
      anObj2 => anObj1[key1] === anObj2[key2]
    ),
    ...anObj1
  })
);

const theme = createTheme({
  status: {
    danger: '#FFBE40',
  },
  palette: {
    primary: {
      main: '#1A77CD',
      darker: '#262626',
      contrastText: '#F0F0F0',
    },
    success: {
      main: '#0EAB4D',
      contrastText: '#F0F0F0',
    },
    background: {
      default: '#262626',
    }
  },
  typography: {
    h4: {
      color: '#F0F0F0',
    },
    h5: {
      color: '#F0F0F0',
    }
  },
});

function App() {
  const [veJoeData, setVeJoeData] = React.useState([]);
  const [latestBlock, setLatestBlock] = React.useState("");
  const [loading, setLoading] = React.useState(false);

  React.useEffect(() => {
    handleRefreshData();
  }, [])

  const formatNumber = (number) => {
    // this puts commas into the number eg 1000 goes to 1,000,
    // i pulled this from stack overflow, i have no idea how it works
    if (number === "loading...") {
      return number;
    } else {
      return Math.floor(number)
        .toString()
        .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
    }
  };

  const currencyFormatter = (params) => {
    return '$' + formatNumber(params.value);
  };

  const columns = [
    {
      field: 'id', headerClassName: 'yy-theme--header', headerName: 'User Address', width: 250,
      renderCell: (params) => (
        <Link href={`https://snowtrace.io/address/${params.value}`} target="_blank" color="primary.contrastText">{params.value}</Link>
      )
    },
    { field: 'joeStaked', headerClassName: 'yy-theme--header', headerName: 'JOE Staked', type: 'number', width: 100, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'joeStakedUSD', headerClassName: 'yy-theme--header', headerName: 'JOE Staked USD', type: 'number', width: 100, valueFormatter: currencyFormatter },
    { field: 'totalVeJoeMinted', headerClassName: 'yy-theme--header', headerName: 'Total veJOE Minted', type: 'number', width: 100, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'totalVeJoeBurned', headerClassName: 'yy-theme--header', headerName: 'Total veJOE Burned', type: 'number', width: 100, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'veJoeBalance', headerClassName: 'yy-theme--header', headerName: 'veJOE Balance', type: 'number', width: 100, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'boostedFarmBalanceUSD', headerClassName: 'yy-theme--header', headerName: 'Boosted TVL USD', type: 'number', width: 100, valueFormatter: currencyFormatter },
    { field: 'joeStakedRanking', headerClassName: 'yy-theme--header', headerName: 'Ranking (Staked JOE)', type: 'number', width: 80, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'veJoeBalanceRanking', headerClassName: 'yy-theme--header', headerName: 'Ranking (veJOE Balance)', type: 'number', width: 80, valueFormatter: (params) => { return formatNumber(params.value) } },
    { field: 'boostedFarmBalanceRanking', headerClassName: 'yy-theme--header', headerName: 'Ranking (Boosted TVL)', type: 'number', width: 80, valueFormatter: (params) => { return formatNumber(params.value) } },
  ];

  const handleLatestBlock = async (newLatestBlock) => {

    setLatestBlock(newLatestBlock);

    // Start by getting all of the information for veJOE stakers.
    const veJoeClient = new ApolloClient({
      uri: VEJOE_API_URL,
      cache: new InMemoryCache(),
    });

    let last_vejoe_id = "";
    let all_vejoe_users = [];
    let new_vejoe_users = [0];

    while (new_vejoe_users.length > 0) {
      new_vejoe_users = await veJoeClient
        .query({
          query: gql(USER_VEJOE_QUERY),
          variables: {
            id: last_vejoe_id,
            latestBlock: Number(newLatestBlock),
          },
        })
      new_vejoe_users = new_vejoe_users["data"]["users"]
      all_vejoe_users = all_vejoe_users.concat(new_vejoe_users)
      if (new_vejoe_users.length > 0) {
        last_vejoe_id = new_vejoe_users[new_vejoe_users.length - 1]["id"]
      }
    }
    all_vejoe_users = all_vejoe_users.sort((a, b) => Number(b.joeStaked) - Number(a.joeStaked))
      .map((user, i) => ({ joeStakedRanking: i + 1, ...user }))
    all_vejoe_users = all_vejoe_users.sort((a, b) => Number(b.veJoeBalance) - Number(a.veJoeBalance))
      .map((user, i) => ({ veJoeBalanceRanking: i + 1, boostedFarmBalanceUSD: 'loading...', boostedFarmBalanceRanking: 'loading...', ...user }))

    // May as well update the data now before getting the rest of the information (which can be slower).
    setVeJoeData(all_vejoe_users);

    // Now we get the pairs which are under the boosted masterchef contract
    const boostedPairsClient = new ApolloClient({
      uri: BOOSTED_API_URL,
      cache: new InMemoryCache(),
    });
    let boostedPairs = []
    boostedPairs = await boostedPairsClient
      .query({
        query: gql(BOOSTED_PAIRS_QUERY),
        variables: {
          latestBlock: Number(newLatestBlock),
        }
      })
    boostedPairs = boostedPairs["data"]["pools"].map((pool) => pool.pair)

    // Here we get the data to show TVL for the pairs, from which we can get TVL for the farm pool, and by extension, for individual addresses.
    const exchangeClient = new ApolloClient({
      uri: EXCHANGE_API_URL,
      cache: new InMemoryCache(),
    });

    let pairs = [];
    pairs = await exchangeClient
      .query({
        query: gql(PAIRS_EXCHANGE_QUERY),
        variables: {
          id_list: boostedPairs,
          latestBlock: Number(newLatestBlock),
        }
      })
    pairs = pairs["data"]["pairs"]

    // Finally we get the balances for users in all of the boosted farms
    const userBoostedClient = new ApolloClient({
      uri: BOOSTED_API_URL,
      cache: new InMemoryCache(),
    });

    let last_id = "";
    let all_users = [];
    let new_users = [0];

    while (new_users.length > 0) {
      new_users = await userBoostedClient
        .query({
          query: gql(USER_BALANCE_QUERY),
          variables: {
            id: last_id,
            latestBlock: Number(newLatestBlock),
          },
        })
      new_users = new_users["data"]["users"]
      all_users = all_users.concat(new_users)
      if (new_users.length > 0) {
        last_id = new_users[new_users.length - 1]["id"]
      }
    }
    all_users = all_users.map((user) => ({
      id: user.id,
      pool_id: user.pool.id,
      pool_pair: user.pool.pair,
      pool_balance: user.pool.balance,
      pool_jlp_balance: user.pool.jlpBalance,
      address: user.address,
      amount: user.amount,
    }))
    all_users = leftJoin(all_users, pairs, "pool_pair", "id")

    // Here we do the calculation/aggregation to get the individual user TVLs
    all_users = all_users.map((user) => ({
      address: user.address,
      boostedFarmBalanceUSD: (Number(user.amount) / Number(user.pool_balance)) * Number(user.pool_jlp_balance) * Number(user.reserveUSD) / Number(user.totalSupply)
    }))
    // Now we aggregate per address
    all_users = all_users.reduce((res, row) => {
      if (!(row.address in res)) {
        res.__array.push(res[row.address] = row);
      } else {
        res[row.address].boostedFarmBalanceUSD += row.boostedFarmBalanceUSD;
      }
      return res;
    }, { __array: [] }).__array

    // Now we join the data together
    all_users = leftJoin(all_vejoe_users.map(({ boostedFarmBalanceUSD, boostedFarmBalanceRanking, ...others }) => others), all_users, "id", "address", { "boostedFarmBalanceUSD": 0 });

    all_users = all_users.sort((a, b) => Number(b.boostedFarmBalanceUSD) - Number(a.boostedFarmBalanceUSD))
      .map((user, i) => ({ boostedFarmBalanceRanking: i + 1, ...user }))

    // And finally we update our state.
    setVeJoeData(all_users);
    setLoading(false);

  };

  const handleRefreshData = async () => {

    setLoading(true);

    const latestBlockClient = new ApolloClient({
      uri: LATEST_BLOCK_API_URL,
      cache: new InMemoryCache(),
    });

    let veJoeLatestBlockNumber = ""
    veJoeLatestBlockNumber = await latestBlockClient
      .query({
        query: gql(LATEST_BLOCK_QUERY),
        variables: {
          subgraphName: "traderjoe-xyz/vejoe",
        }
      })
    veJoeLatestBlockNumber = veJoeLatestBlockNumber["data"]["indexingStatusForCurrentVersion"]["chains"][0]["latestBlock"]["number"]

    let boostedLatestBlockNumber = ""
    boostedLatestBlockNumber = await latestBlockClient
      .query({
        query: gql(LATEST_BLOCK_QUERY),
        variables: {
          subgraphName: "traderjoe-xyz/boosted-master-chef",
        }
      })
    boostedLatestBlockNumber = boostedLatestBlockNumber["data"]["indexingStatusForCurrentVersion"]["chains"][0]["latestBlock"]["number"]

    let exchangeLatestBlockNumber = ""
    exchangeLatestBlockNumber = await latestBlockClient
      .query({
        query: gql(LATEST_BLOCK_QUERY),
        variables: {
          subgraphName: "traderjoe-xyz/exchange",
        }
      })
    exchangeLatestBlockNumber = exchangeLatestBlockNumber["data"]["indexingStatusForCurrentVersion"]["chains"][0]["latestBlock"]["number"]

    handleLatestBlock(Math.min.apply(Math, [veJoeLatestBlockNumber, boostedLatestBlockNumber, exchangeLatestBlockNumber]));
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container sx={{
        height: '100vh',
        padding: "10px",
        '& .super-app-theme--yy': {
          bgcolor: "success.main",
        }
      }}>
        <Grid container spacing={2} align="center" sx={{ height: '100%', }}>
          <Grid container item alignItems="center">
            <Grid item xs={6} sm={4}>
              <Typography variant="h4">Top veJOE stakers</Typography>
            </Grid>
            <Grid item xs={6} sm={4}>
              <Typography variant="h5">Data as of block: {latestBlock}</Typography>
            </Grid>
            <Grid item xs={6} sm={4}>
              <Box sx={{ m: 1, position: 'relative' }}>
                <Button
                  variant="contained"
                  onClick={handleRefreshData}
                  disabled={loading}
                >
                  Refresh data
                </Button>
                {loading && (
                  <CircularProgress
                    size={24}
                    sx={{
                      color: "white",
                      position: 'absolute',
                      top: '50%',
                      left: '50%',
                      marginTop: '-12px',
                      marginLeft: '-12px',
                    }}
                  />
                )}
              </Box>
            </Grid>
          </Grid>
          <Grid item xs={12} height={"90vh"}>
            <DataGrid
              rows={veJoeData}
              columns={columns}
              density='compact'
              headerHeight={80}
              // pageSize={20}
              initialState={{
                sorting: {
                  sortModel: [{ field: 'joeStaked', sort: 'desc' }],
                },
              }}
              rowsPerPageOptions={[10, 25, 50, 100]}
              getRowClassName={(params) => `super-app-theme--${params.row.id === "0xe7462905b79370389e8180e300f58f63d35b725f" ? "yy" : "other"}`}
              sx={{
                boxShadow: 2,
                border: 2,
                borderColor: 'success.main',
                color: 'primary.contrastText',
                '& .MuiDataGrid-cell:hover': {
                  color: 'primary.main',
                },
                '& .MuiDataGrid-columnHeaders': {
                  backgroundColor: 'success.main',
                },
                '& .MuiDataGrid-columnHeaderTitle': {
                  textOverflow: "clip",
                  whiteSpace: "break-spaces",
                  lineHeight: 1
                },
                '& .MuiTablePagination-root': {
                  color: 'primary.contrastText',
                },
                '& .MuiSelect-icon': {
                  color: 'primary.contrastText',
                },
                '& .MuiButtonBase-root': {
                  color: 'primary.contrastText',
                },
                '& .MuiMenuItem-root': {
                  color: 'primary.darker',
                },
              }}
            />
          </Grid>
        </Grid>
      </Container>
    </ThemeProvider>
  );
}

export default App;
