
export const LATEST_BLOCK_QUERY = `
query indexingStatusForCurrentVersion($subgraphName: String) {
  indexingStatusForCurrentVersion(subgraphName: $subgraphName) {
      chains(network: "avalanche") {
          network
          latestBlock {
              hash
              number
          }
      }
  }
}
`

export const USER_VEJOE_QUERY = `
query users($id: String, $latestBlock: Int) {
  users(first: 1000, orderBy: id, orderDirection: asc, where: {id_gt: $id}, block: {number: $latestBlock}) {
    id
    joeStaked
    joeStakedUSD
    totalVeJoeMinted
    totalVeJoeBurned
    veJoeBalance
  }    
}
`

export const USER_BALANCE_QUERY = `
query users($id: String, $latestBlock: Int) {
  users(first: 1000, orderBy: id, orderDirection: asc, where: {id_gt: $id, amount_gt: 0}, block: {number: $latestBlock}) {
    id
    pool {
      id
      pair
      balance
      jlpBalance
    }
    address
    amount
  }
}
`

export const BOOSTED_PAIRS_QUERY = `
query pools($latestBlock: Int) {
    pools(first: 1000, block: {number: $latestBlock}) {
        pair
    }
}
`

export const PAIRS_EXCHANGE_QUERY = `
query pools($id_list: [Bytes], $latestBlock: Int) {
    pairs(first: 1000, where: {id_in: $id_list}, block: {number: $latestBlock}) {
        id
        name
        reserveUSD
        totalSupply
      }
}
`